      },
      initMap() {

        const map = L.map('map');
        const layer = Tangram.leafletLayer({
          scene: {
            cameras: {
              camera1: {
                type: "perspective"
              }
            },
            lights: {
              light1: {
                type: "directional",
                direction: [0, 1, "-.5"],
                diffuse: 0.3,
                ambient: 1
              }
            },
            sources: {
              mapzen: {
                type: "TopoJSON",
                url: "https://tile.mapzen.com/mapzen/vector/v1/all/{z}/{x}/{y}.topojson",
                url_params: {
                  api_key: "mapzen-tDHBHDp"
                },
                max_zoom: 16
              }
            },
            layers: {
              earth: {
                data: {
                  source: "osm"
                },
                draw: {
                  polygons: {
                    order: "function() { return feature.sort_rank; }",
                    color: "#ff00ff"
                  }
                }
              }
            },
          }
        });
        layer.scene.subscribe({
          load: () => {
            layer.scene.setDataSource('osm', {
              type: 'TopoJSON',
              url: 'https://tile.mapzen.com/mapzen/vector/v1/all/{z}/{x}/{y}.topojson',
              url_params: {
                api_key: "mapzen-tDHBHDp"
              },
            });
          },
          view_complete: () => {}
        });
        layer.addTo(map);
        map.setView([40.70531887544228, -74.00976419448853], 15);
      },
      initMap1() {
        var map = L.map('map', {
          crs: L.CRS.EPSG3857
        });
        var layer = Tangram.leafletLayer({
          scene: '../static/scene.yaml'
        });
        layer.addTo(map);
        map.setView([40.7373046875, -73.9599609375], 12);
      },
    }
  }

</script>

