<style scoped>
  #maphome {
    position: fixed;
    top: 0;
    bottom: 0;
    left: 0;
    right: 0;
    background-image: url(../../../static/background.jpg);
    background-repeat: no-repeat;
    background-size: cover;
    overflow-y: hidden;
  }

  #headText {
    color: white;
    margin: 50px;
    font-size: 20px;
  }

  #mapcontent {
    display: flex;
    justify-content: center;
  }

  #home-cartoon {
    width: 100%;
    position: fixed;
    left: 0;
    top: 0;
    z-index: -1;
  }

  #map {
    margin: 0 auto;
    height: 750px;
    width: 1200px;
  }

</style>



